import { Component, OnInit } from '@angular/core';
import { MenuController, AlertController, NavController, Events } from '@ionic/angular';
import { HttpClient } from '@angular/common/http';
//import { Http, Headers } from '@ionic/http';
import { RestService } from '../provider/rest.service';
import { Network } from '@ionic-native/network/ngx';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  type: any = 'password';
  username: any = '';
  password: any = '';
  rememberme: any = '';
  result: any;
  //  mode: any = 'Online';
  constructor(public menuCtrl: MenuController, private network: Network, public http: HttpClient, public navCtrl: NavController, private RestService: RestService, public alertCtrl: AlertController, private events: Events) { }

  ngOnInit() {
    // this.rememberme=true;
  }

  ionViewDidEnter() {
    if (localStorage.getItem('remember')) {
      this.rememberme = false;
      this.username = localStorage.getItem('username');
      this.password = localStorage.getItem('password');
    }
    // else {
    //   this.rememberme = false;
    //   this.username = '';
    //   this.password = '';
    // }
  }

  checkRemember(event) {
    this.rememberme = event.target.checked;
  }

  async login() {


    if (this.username == '' || this.username == undefined) {
      const alert = await this.alertCtrl.create({
        cssClass: 'cstmAlert',
        message: 'Please enter Username.',
        buttons: ['OK']
      });
      await alert.present();
      return;
    }
    if (this.password == '' || this.password == undefined) {
      const alert = await this.alertCtrl.create({
        cssClass: 'cstmAlert',
        message: 'Please enter Password.',
        buttons: ['OK']
      });
      await alert.present();
      return;
    }
    if (this.network.type === "none") {
      const alert = await this.alertCtrl.create({
        cssClass: 'cstmAlert',
        message: 'Please Check Internet Connection.',
        buttons: ['OK']
      });
      await alert.present();
      return;
    }

    var remember = this.rememberme;
    var data = {
      username: this.username,
      password: this.password,
      DeviceId: localStorage.deviceID,
      AppToken: localStorage.token
    };
    this.RestService.loginApi(data).then(async (response) => {
      this.result = response;
      if (this.result.Message == "success") {
        if (remember) {
          localStorage.setItem('remember', remember);
          localStorage.setItem('username', this.username);
          localStorage.setItem('password', this.password);
        } else {
          localStorage.setItem('remember', remember);
          localStorage.removeItem('username');
          localStorage.removeItem('password');
        }
        localStorage.setItem('userData', JSON.stringify(this.result.result));
        // localStorage.setItem('modeType', this.mode)
        this.getProfileData();
        // this.navCtrl.navigateRoot("/dashboard");
      } else {
        const alert = await this.alertCtrl.create({
          cssClass: 'cstmAlert',
          message: 'Username or Password incorrect!.',
          buttons: ['OK']
        });
        alert.present();
      }


    });




  }

  async getProfileData() {
    var Token = JSON.parse(localStorage.userData).Token;
    this.RestService.requestViaGet('app/EngineerProfile/GetEngineerProfile/' + Token, '').then(async (response) => {
      console.log(response);
      this.result = response;
      if (this.result.Message == "success") {
        var profileData = this.result.result[0];
        localStorage.setItem('profileData', JSON.stringify(profileData));
        this.events.publish('user:logged', profileData);
        this.navCtrl.navigateRoot("/dashboard");
      } else {
        const alert = await this.alertCtrl.create({
          cssClass: 'cstmAlert',
          message: 'Something went wrong!.',
          buttons: ['OK']
        });
        alert.present();
      }


    });
  }
  // ionViewWillEnter() {
  // //  this.menuCtrl.enable(false);
  // }

  viewPass() {
    if (this.type == 'password') {
      this.type = 'text';
    } else {
      this.type = 'password'
    }
  }



}
